import React, { useState, useContext } from "react";
import { FirebaseContext } from "../firebase";

import { Link } from "react-router-dom";

const SignUp = (props) => {
  const firebase = useContext(FirebaseContext);

  const data = {
    pseudo: "",
    email: "",
    pwd: "",
    pwd2: "",
  };
  const [loginData, setLoginData] = useState(data);

  const [error, setError] = useState("");

  const handleChange = (e) => {
    setLoginData({ ...loginData, [e.target.id]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const { email, pwd } = loginData;
    firebase
      .signUpUser(email, pwd)
      .then((user) => {
        setLoginData({ ...data });
        props.history.push("/");
      })
      .catch((error) => {
        setError(error);
        setLoginData({ ...data });
      });
  };

  const { pseudo, email, pwd, pwd2 } = loginData;

  const btn =
    pseudo === "" || email === "" || pwd === "" || pwd !== pwd2 ? (
      <button disabled>Inscription</button>
    ) : (
      <button>Inscription</button>
    );

  //gestion des erreurs
  const errorMsg = error !== "" && <span>{error.message}</span>;

  return (
    <div className="col col-6 m-5">
      {errorMsg}
      <h1>Inscription</h1>
      <form onSubmit={handleSubmit} className="d-flex flex-column">
        <label>Pseudo</label>
        <input
          onChange={handleChange}
          value={pseudo}
          type="text"
          placeholder="Pseudo"
          id="pseudo"
          autoComplete="off"
          required
        />
        <label>Email</label>
        <input
          onChange={handleChange}
          value={email}
          type="email"
          placeholder="Email"
          id="email"
          required
        />
        <label>Mot de passe</label>
        <input
          onChange={handleChange}
          value={pwd}
          type="password"
          placeholder="Mot de passe"
          id="pwd"
          autoComplete="off"
          required
        />
        <label>Confirmez votre mot de passe</label>
        <input
          onChange={handleChange}
          value={pwd2}
          type="password"
          placeholder="Mot de passe"
          id="pwd2"
          autoComplete="off"
          required
        />
        {btn}
      </form>
      <div>
        <Link to="/login">Déjà inscrit? Connectez vous</Link>
      </div>
    </div>
  );
};

export default SignUp;
